import React from "react";
import Icon from "@ant-design/compatible/lib/icon";
import { Layout, Menu } from "antd";
import { useRouter } from "next/router";
import { useSelector } from "react-redux";

const { Sider } = Layout;
const CSidebar = ({ collapsed, height }) => {
  const { menus } = useSelector((state) => state.menus);
  const router = useRouter();
  function getItem(label, key, icon, children) {
    return {
      key,
      icon,
      children,
      label,
    };
  }

  const items = menus.map((menu, idx) => {
    return menu.children === undefined
      ? getItem(
          <a
            href={menu.path}
            onClick={(e) => {
              e.preventDefault();
              router.push(menu.path);
            }}
          >
            {menu.label}
          </a>,
          menu.path + idx,
          <Icon type={menu.icon} />
        )
      : getItem(
          menu.label,
          menu.path + idx,
          <Icon type={menu.icon} />,
          menu.children.map((sub, idx) => {
            return getItem(
              <a
                href={sub.path}
                onClick={(e) => {
                  e.preventDefault();
                  router.push(sub.path);
                }}
              >
                {sub.label}
              </a>,
              sub.path + idx,
              <Icon type={sub.icon} />
            );
          })
        );
  });

  return (
    <Sider
      style={{
        overflow: "auto",
        height: height + 16,
        marginTop: 64,
      }}
      width={220}
      trigger={null}
      collapsible
      collapsed={collapsed}
    >
      <Menu theme="dark" className="my-menu" mode="inline" items={items} />
    </Sider>
  );
};
export default CSidebar;
